import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';

import {BrowserRouter} from "react-router-dom";

import {Provider} from "react-redux";
import {combineReducers, createStore} from "redux";


let alertdefault = true

function reducer2(state = alertdefault,action){
  if(action.type === 'closealert'){
    state = false
    return state
  }
  else {
    return state
  }
}

let defaultstate = [
    {
      id: 0,
      name: 'cool shoes',
      quantity: 1
    },
    {
      id: 1,
      name: 'cool shoes1',
      quantity: 2
    },
    {
      id: 2,
      name: 'cool shoes2',
      quantity: 3
    },
    {
      id: 3,
      name: 'cool shoes3',
      quantity: 4
    },
    {
      id: 4,
      name: 'cool shoes4',
      quantity: 5
    }
  ]



function reducer(state = defaultstate, action){
  if(action.type === 'addincart'){

    let copy = [...state]
    defaultstate.map(function (a,i){
      if(action.payload.id === copy[i].id){
        copy[i].quantity++
      }
      else
        copy.push(action.payload)
    })
    return copy
  }

  else if(action.type === 'increasing'){

    let copy = [...state]
    copy[0].quantity++;

    return copy
  }
  else if(action.type === 'decreasing'){

    let copi = [...state]
    copi[0].quantity--;

    return copi
  }

  else{
    return state
  }

}

let store = createStore(combineReducers({reducer, reducer2}))


ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Provider store={store}>
        <App />
      </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
