import React from "react";
import {Table} from "react-bootstrap";
import {connect, useDispatch, useSelector} from "react-redux";

function Cart(props){

  let state = useSelector((state)=> state)
  let dispatch = useDispatch()
  return(
    <div>

      <div>
        <Table responsive="sm">

          <thead>
          <tr>
            <th>#</th>
            <th>Product name</th>
            <th>Quantity</th>
            <th>Modification</th>
          </tr>
          </thead>

          <tbody>

          {
            state.reducer.map(function(a,i){
              return(
                <tr key={i}>
                  <td>{[i]}</td>
                  <td>{a.name}</td>
                  <td>{a.quantity}</td>
                  <td>
                    <button onClick={()=>{
                      dispatch({type: 'increasing'})
                    }}>+</button>
                    <button onClick={()=>{
                      dispatch({type: 'decreasing'})
                    }}>-</button>
                  </td>
                </tr>
              )
            })
          }

          </tbody>
        </Table>
        {props.alerttest === true
          ? <div className="my-alert2">
            <p>discount for new comers </p>
            <button onClick={() => {
              props.dispatch({type: 'closealert'})
            }}>close</button>
          </div>
          : null
        }
      </div>

    </div>
  )
}

