import React, {useEffect, useState} from "react";
import {useHistory, useParams} from "react-router-dom";
import styled from 'styled-components';
import '../css/ProductDetail.scss'
import {Nav, Tab, TabContent} from "react-bootstrap";
import {CSSTransition} from "react-transition-group";

import {connect} from "react-redux";

let Box = styled.div` //Box is a component, name should start with capital letter
  padding: 20px;
`;

let Jaemok = styled.h4`
  font-size: 25px;
  color : ${ props => props.saek}
`;


function ProductDetail(props){

  let [alertn, changealertn] = useState(true)
  let [clickedtab, changeclickedtab] = useState(0)
  let [switchani, changeswitchani] = useState(false)


  useEffect( () => {
    let timer = setTimeout( () => {
      changealertn(false)
      return () => { clearTimeout(timer)} // when you load other page during alertn function is on, clear timer
    },2000)
  },[]); // [] is for preventing not to be executed again during updating

  let{ id } = useParams();
  let productID = props.shoes.find(function (product){
    return product.id == id // props.shoes.find( product => product.id == id)
  })
  let history = useHistory();


  return(
    <div className="container">
      <Box>
        <Jaemok saek = "blue">desctiptionnnnn</Jaemok>
        <Jaemok saek = {'orange'}>desctiptionnnnn</Jaemok>
        <Jaemok className = "test">Test</Jaemok>
      </Box>

      <input/>


      {
        alertn == true
        ? (
          <div className="my-alert">
            <p>selling fast</p>
          </div>
          )
        : null
      }
      <div className="row">
        <div className="col-md-6">
          <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{productID.title}</h4>
          <p>{productID.content}</p>
          <p>{productID.price}</p>
          <StockInfo stock ={props.stock}></StockInfo>
          <button className="btn btn-danger" onClick={() => {
            props.dispatch({type : 'addincart', payload : {id: 2, name : 'new product', quantity: 1}})
            history.push('/cart')
          }}>주문하기</button>
          {/*//make function to change the stock when you click the button*/}
          <button className="btn btn-danger" onClick={() => {
            history.push('/')
          }}>go back</button>
        </div>
      </div>


      <Nav className="mt-5" variant="tabs" defaultActiveKey="link-0">
        <Nav.Item>
          <Nav.Link eventKey="link-0" onClick={ () => {
            changeclickedtab(0)
            changeswitchani(false)
          }}>Active</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link-1" onClick={ () => {
            changeclickedtab(1)
            changeswitchani(false)
          }}>Option 2</Nav.Link>
        </Nav.Item>
      </Nav>

      <CSSTransition in={switchani} classNames="wow" timeout={500}>
        <Tabtab clickedtab = {clickedtab}  changeswitchani = {changeswitchani}/>
      </CSSTransition>

    </div>
  )
}

function Tabtab(props){

  useEffect( () => {
    props.changeswitchani(true)
  })

 if (props.clickedtab === 0) {
   return <div>000000</div>
 }
 else if (props.clickedtab === 1) {
   return <div>11111</div>
 }
}



function StockInfo(props){
  return (
    <div>
      <p>Stock : {props.stock[1]}</p>
    </div>
  )
}

function testie(state){
  console.log(state)
  return{
    state : state.reducer,
    alerttest : state.reducer2
  }
}


export default connect(testie)(ProductDetail);